/* Register.css */
.register {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 60vh;
  background: radial-gradient(circle at top, #2b5876, #4e4376);
  padding: 15px;
}

.register form {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  width: 85%;
  max-width: 350px;
  text-align: center;
  transition: 0.3s;
}

.register form:hover {
  transform: scale(1.02);
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.3);
}

.register h1 {
  font-size: 22px;
  font-weight: bold;
  color: #fff;
  margin-bottom: 12px;
}

.register .form-control {
  font-size: 14px;
  padding: 8px;
  margin-bottom: 10px;
  border-radius: 6px;
  border: none;
  width: 100%;
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
  outline: none;
  transition: 0.3s;
}

.register .form-control:focus {
  background: rgba(255, 255, 255, 0.3);
  box-shadow: 0 0 5px rgba(255, 255, 255, 0.5);
}

.register button {
  width: 100%;
  padding: 8px;
  font-size: 14px;
  font-weight: bold;
  color: #fff;
  background: linear-gradient(45deg, #ff416c, #ff4b2b);
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: 0.3s;
}

.register button:hover {
  background: linear-gradient(45deg, #ff4b2b, #ff416c);
  box-shadow: 0 3px 8px rgba(255, 65, 108, 0.5);
}

@media (max-width: 600px) {
  .register form {
      padding: 15px;
  }
}
