/* HomePage.css */

.homepage-container {
    display: flex;
    gap: 20px;
    padding: 20px;
    background: linear-gradient(135deg, #ff9a9e, #fad0c4);
    min-height: 100vh;
  }
  
  .filter-section {
    flex: 1;
    background: #fff3e0;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .products-section {
    flex: 3;
    text-align: center;
    background: #ffffff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .products-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
    padding: 20px 0;
  }
  
  .product-card {
    background: linear-gradient(135deg, #84fab0, #8fd3f4);
    border-radius: 15px;
    box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.15);
    padding: 15px;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    text-align: center;
  }
  
  .product-card:hover {
    transform: scale(1.08);
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
  }
  
  .product-card img {
    width: 100%;
    border-radius: 10px;
  }
  
  .card-details {
    padding: 10px 0;
  }
  
  .price {
    font-weight: bold;
    color: #ff4081;
    font-size: 1.2em;
  }
  
  .details-button, .cart-button, .reset-button, .loadmore-button {
    background: linear-gradient(135deg, #ff758c, #ff7eb3);
    color: white;
    border: none;
    padding: 12px 20px;
    margin: 5px;
    border-radius: 8px;
    font-weight: bold;
    cursor: pointer;
    transition: background 0.3s ease, transform 0.2s ease;
  }
  
  .details-button:hover, .cart-button:hover, .reset-button:hover, .loadmore-button:hover {
    background: linear-gradient(135deg, #ff6b81, #ff4d6d);
    transform: scale(1.05);
  }
  
  @media (max-width: 768px) {
    .homepage-container {
      flex-direction: column;
    }
    .products-grid {
      grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    }
  }
  